.footer {
  padding: 36px 0 50px;
  &__inner {
    max-width: 800px;
    margin: 0 auto;
  }
  &__date {
    display: flex;
    align-items: flex-end;
    span:nth-child(1) {
      font-size: 22.5px;
      line-height: 120%;
      font-weight: 700;
      color: #2f1b15;
    }
    span:nth-child(2) {
      font-family: 'Courier New';
      font-size: 30px;
      line-height: 120%;
      font-weight: 400;
      color: #231f20;
      margin-left: 18px;
      padding-bottom: 3px;
      border-bottom: 3px solid #231f20;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 25px 0;
    &__date {
      span:nth-child(1) {
        font-size: 20px;
      }
      span:nth-child(2) {
        font-size: 20px;
        padding-bottom: 2px;
        border-bottom: 2px solid #231f20;
        margin-left: 12px;
      }
    }
  }
}