.main {
  padding: 10px 0 36px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 221px;
    margin: 0 auto;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__form {
    margin-top: 60px;
    &-group {
      display: flex;
      align-items: center;
      margin: 0;
      padding: 0;
      border: unset;
      &:not(:first-child) {
        margin-top: 25px;
      }
      span {
        font-size: 22.5px;
        line-height: 120%;
        font-weight: 700;
        color: #2f1b15;
        flex-shrink: 0;
      }
      input {
        font-family: 'Courier New';
        font-size: 30px;
        line-height: 120%;
        font-weight: 400;
        color: #231f20;
        width: 100%;
        background: transparent;
        border: unset;
        border-bottom: 3px solid #231f20;
        margin-left: 15px;
        &::placeholder {
          color: #231f20;
        }
      }
    }
  }
}
.checkbox {
  padding: 36px 0;
  &__info {
    font-size: 15px;
    line-height: 120%;
    font-weight: 400;
    color: #3d3d3d;
    margin-top: 18px;
  }
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-top: 60px;
  }
  &__check {
    display: flex;
    align-items: center;
    position: relative;
    &:not(:first-child) {
      margin-top: 15px;
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      margin: 0;
      cursor: pointer;
      &:checked ~ label {
        &:before {
          opacity: 1;
        }
      }
    }
    label {
      font-size: 21px;
      line-height: 120%;
      font-weight: 700;
      color: #000000;
      padding-left: 26px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 18px;
        height: 18px;
        border: 2px solid #000000;
      }
      &:before {
        content: '';
        position: absolute;
        top: -6px;
        left: 2px;
        width: 21px;
        height: 27px;
        background-image: url("../img/check.png");
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0;
        transition: all 0.5s;
      }
    }
  }
}
.js {
  padding: 36px 0 20px;
  &__info {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
    border-top: 3px solid #2f1b15;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 3px;
      height: 25px;
      background: #2f1b15;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 3px;
      height: 25px;
      background: #2f1b15;
    }
  }
  &__text {
    font-size: 13px;
    line-height: 120%;
    font-weight: 700;
    color: #2f1b15;
    padding-top: 30px;
    position: relative;
    &--active {
      &:before {
        content: '';
        position: absolute;
        top: -20px;
        left: -8px;
        width: 0;
        height: 0;
        border: 9px solid transparent;
        border-top: 12px solid #2f1b15;
      }
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 2px;
      height: 11px;
      background: #2f1b15;
    }
  }
}
.info {
  &__desc {
    font-size: 15px;
    line-height: 120%;
    font-weight: 400;
    color: #3d3d3d;
    margin-top: 20px;
  }
  &__about {
    margin-top: 30px;
    position: relative;
    overflow: hidden;
    p {
      font-family: 'Courier New';
      font-size: 30px;
      line-height: 150%;
      font-weight: 400;
      color: #3d3d3d;

    }
    p[contenteditable="true"]:focus {
      outline: unset;
    }
  }
  &__line {
    position:absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    pointer-events: none;
    span {
      display:inline-block;
      width:100%;
      border-bottom: solid 3px #3d3d3d;
      &:not(:first-child) {
        margin-top: 39px;
      }
    }
  }
}
.plans {
  padding: 36px 0;
  &__desc {
    font-size: 15px;
    line-height: 120%;
    font-weight: 400;
    color: #3d3d3d;
    margin-top: 12px;
  }
  &__checkboxes {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  &__check {
    display: flex;
    align-items: center;
    position: relative;
    &:not(:first-child) {
      margin-left: 25px;
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      margin: 0;
      cursor: pointer;
      &:checked ~ label {
        &:before {
          opacity: 1;
        }
      }
    }
    label {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #000000;
      padding-left: 26px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -3px;
        left: -6px;
        width: 28px;
        height: 28px;
        background-image: url("../img/check2.webp");
        background-repeat: no-repeat;
        background-position: center;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 6px;
        transform: translate(0, -50%);
        width: 6px;
        height: 6px;
        background: #000000;
        border-radius: 50%;
        transition: all 0.5s;
        opacity: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    padding: 25px 0;
    &__title {
      width: 180px;
    }
    &__form {
      margin-top: 30px;
      &-group {
        span {
          font-size: 20px;
        }
        input {
          font-size: 20px;
        }
      }
    }
  }
  .checkbox {
    padding: 25px 0;
    &__grid {
      grid-template-columns: 1fr 1fr;
      margin-top: 30px;
    }
    &__check {
      label {
        font-size: 16px;
        &:after {
          top: 0;
        }
        &:before {
          top: -10px;
        }
      }
    }
  }
  .js {
    padding: 25px 0;
    &__info {
      display: block;
      border-top: unset;
      border-left: 2px solid #2f1b15;
      padding: 30px 0;
      margin-top: 30px;
      margin-left: 30px;
      &:after {
        width: 25px;
        height: 2px;
      }
      &:before {
        top: unset;
        left: 0;
        bottom: 0;
        width: 25px;
        height: 2px;
      }
    }
    &__text {
      padding: 0 0 0 30px;
      &:not(:first-child) {
        margin-top: 50px;
      }
      &--active {
        &:before {
          top: -8px;
          left: -20px;
          border: 9px solid transparent;
          border-left: 12px solid #2f1b15;
        }
      }
      &:after {
        width: 11px;
        height: 2px;
      }
    }
  }
  .info {
    &__about {
      p {
        font-size: 20px;
      }
    }
    &__line {
      span:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
  .plans {
    padding: 25px 0;
    &__checkboxes {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 25px;
    }
    &__check {
      &:not(:first-child) {
        margin: 0;
      }
      label {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__form {
      &-group {
        flex-direction: column;
        align-items: start;
        &:not(:first-child) {
          margin-top: 15px;
        }
        input {
          margin: 10px 0 0 0;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .main {
    &__title {
      width: 100px;
    }
    &__form {
      &-group {
        span {
          font-size: 16px;
        }
        input {
          font-size: 14px;
          margin-top: 5px;
        }
      }
    }
  }
  .checkbox {
    &__grid {
      grid-template-columns: 1fr;
    }
  }
  .info {
    &__about {
      p {
        font-size: 14px;
      }
    }
    &__line {
      span:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
  .plans {
    &__checkboxes {
      grid-template-columns: 1fr;
    }
  }
}