.header {
  padding: 50px 0 0 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    font-family: 'Arkhip';
    display: block;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #2b2b2b;
    text-align: center;
    span {
      display: block;
      font-size: 9px;
      line-height: 120%;
      text-transform: uppercase;
      color: #654724;
    }
  }
  &__img {
    width: 252px;
    img {
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 25px 0 0 0;
    &__img {
      width: 180px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      font-size: 22px;
    }
    &__img {
      width: 150px;
    }
  }
}